import React from 'react';
import { useAsync } from 'react-async';

import { SnapshotWhere } from '../Context';
import { storage } from '../utils/Connection';

import ComponentCartThumb from './ComponentCartThumb';
import ComponentSkeletonCartThumb from './ComponentSkeletonCartThumb';

const defaultGravatar = 'data:image/svg+xml;base64,';

const awaitStorage = async (data, where) => {

  // eslint-disable-next-line no-unused-vars
  const { image_name, image_size, image_format } = data;

  async function addImageProcess(src){
    const img = new Image();
    img.src = src;
    await img.decode();
    return img.src
  }

  function onResolve(foundURL) {
    return addImageProcess(foundURL);
  };

  function onReject(error) {
    return defaultGravatar;
  };

  return await storage()
    .ref(`/${where}/products/thumbs`)
    .child(image_name + '_160x160.jpeg')
    .getDownloadURL()
    .then(onResolve, onReject)
    .catch((error) => defaultGravatar)
};

const fetchPerson = async ({ thumb, useWhere }, { signal }) => {

  const [image] = thumb;
  return await awaitStorage(image, useWhere);
};

function ComponentAwaitCartThumb({ thumb }) {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, thumb, useWhere });

  if (error) return error.message
  if (data) return <ComponentCartThumb {...{ data }} />
  return <ComponentSkeletonCartThumb />
}

export default ComponentAwaitCartThumb;
