import React from 'react';
import { useAsync } from "react-async";

import { SnapshotWhere } from '../Context';
import { functions } from '../utils/Connection';

import ComponentHome from './ComponentHome';
import ComponentSkeletonHome from './ComponentSkeletonHome';

const fetchPerson = async ({ useWhere }, { signal }) => {

  return await functions()
    .httpsCallable('getPosts')({ where: `${useWhere}/feed` })
    .then((snapshot) => snapshot.data);
}

function ComponentAwaitHome({ configGlobal, screenRef, swipeRef, useMenuId, setMenuId }) {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere });

  if (error) return error.message
  if (data) return <ComponentHome {...{ data, configGlobal, screenRef, swipeRef, useMenuId, setMenuId }} />
  return <ComponentSkeletonHome {...{ swipeRef }} />
}

export default ComponentAwaitHome;
