import React, { useState } from 'react';

import { Additional, Info, Handle } from '../styles/Additional';

function ComponentMapAdditional({ data, i, usePost, setPost }) {

  const id = data[0];
  // eslint-disable-next-line no-unused-vars
  const { about, limit, name, value } = data[1];
  const { additional = {} } = usePost;
  const { amount = 0 } = additional[id] || {};

  const [useAnimate, setAnimate] = useState(false);

  // eslint-disable-next-line no-unused-vars
  const altTransition = () => {
    setAnimate(true);
    setTimeout(() => setAnimate(false), 500);
  };

  const altAmount = (val) => {
    if(val) {
      setPost((prevState) => {
        const assoc = {...prevState};
        assoc.additional = {
          ...additional,
          [id]: {
            amount: val
          }
        }
        return assoc;
      });
    }
    else {
      if(Object.keys(additional).includes(id)) {
        if(Object.keys(additional).length > 1) {
          setPost((prevState) => {
            const assoc = { ...prevState };
            delete assoc.additional[id];
            return assoc;
          });
        }
        else {
          setPost((prevState) => {
            const assoc = { ...prevState };
            delete assoc.additional;
            return assoc;
          });
        }
      }
    }
  };

  const altDecrement = () => amount > 0 ? altAmount(amount-1) : altTransition();
  const altIncrement = () => amount < limit ? altAmount(amount+1) : altTransition();

  return (
    <Additional>
      <Info>
        <span>{name} por <strong>{Number(value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</strong></span>
      </Info>
      <Handle animeBtn={useAnimate}>
        <button type="button" onClick={() => altDecrement()}>-</button>
        <input type="text" readOnly value={amount} />
        <button type="button" onClick={() => altIncrement()}>+</button>
      </Handle>
    </Additional>
  )
}

export default ComponentMapAdditional;
