const { jQuery: $ } = window;

// eslint-disable-next-line import/no-anonymous-default-export
export default {
  u: {
    a: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
    b: ["Domingo", "Segunda", "Terça", "Quarta", "Quinta", "Sexta", "Sábado"],
    c: ["Domingo", "Segunda-feira", "Terça-feira", "Quarta-feira", "Quinta-feira", "Sexta-feira", "Sábado"],
    d: ["sun", "mon", "tue", "wed", "thu", "fri", "sat"],
    e: new Date().getDay()
  },
  category_routers: {
    "001": "/hamburguer",
    "002": "/porcoes",
    "003": "/bebidas",
    "004": "/combos",
    "005": "/promocoes",
    "006": "/bomboniere"
  },
  category_names: {
    "001": "Hambúrguer",
    "002": "Porções",
    "003": "Bebidas",
    "004": "Combos",
    "005": "Promoções",
    "006": "Bomboniere"
  },
  isValid: function ({ usePost, data, useAmount, useComment, useSelector, useAdditional, useMoment, useCart }) {

    // eslint-disable-next-line no-unused-vars
    const { amount = 1, comment = '', additional = {}, selector = {} } = usePost;
    // eslint-disable-next-line no-unused-vars
    const { category, description, favorite, grams, price, published, purchase, thumb, title, weight, extra = {}, pick = {} } = data;

    // console.log(
    //   Object.entries(pick)
    //   .filter(data => data[1].important)
    // );
    // console.log(pick);


  const valSelector = () => {

    return Object.keys(selector).length ?
    Object.entries(selector).map((data) => {
      const uid = data[0]
      const bag = data[1];
      const iBag = JSON.parse(useSelector)[uid] || [];

      // bag (valor atual) é menor que iBag(valor em cache)
      return bag.length < iBag.length
        ? iBag.map(value => bag.includes(value)).every(val => !!val)
        : bag.map(value => iBag.includes(value)).every(val => !!val)

    }).includes(false)
    : false
  };

  const valExtra = () => {
    const isValid = val => {
      const uid = val[0];
      const { amount } = val[1];
      const { amount: iAmount = 0 } = JSON.parse(useAdditional)[uid] || {};

      return amount === iAmount;
    };

    return Object.entries(additional).length === Object.entries(JSON.parse(useAdditional)).length
    ? Object.entries(additional).length < Object.entries(JSON.parse(useAdditional)).length
      ? Object.entries(JSON.parse(useAdditional)).map(isValid).includes(false)
      : Object.entries(additional).map(isValid).includes(false)
    : true
  };

    return (Object.keys(pick).length
    ? Object.entries(pick).map(data => data[1].important).includes(true)
      ? Object.entries(pick)
      .filter(data => data[1].important)
      .map(val => {
        const uid = val[0];
        const { important, limit } = val[1];
        const bag = selector[uid] || [];

        return important && bag.length === limit;
      }).every(val => !!val)
      : true
    : true) && (useAmount !== amount || useComment !== comment || valExtra() || valSelector() || !useCart.find((data) => data.moment === useMoment))
  },
  PushAlert: function(data) {
      if(!document.body.contains(document.querySelector('.push-alert'))){
          document.body.insertAdjacentHTML('beforeend', `
          <div class="push-alert">
              <p>${data}</p>
          </div>`);

          $('.push-alert').animate({
              'transform': '70'
          }, {
              step: function (now, fx) {
                  $(this).css({"transform": "translate3d(-50%, " + now + "px, 0px)"});
              },
              duration: 150,
              easing: 'linear',
              queue: false,
              complete: function () {
                  setTimeout(()=>{
                      $(this).animate({ opacity: 0 }, {
                          duration : 300,
                          easing: 'linear',
                          queue: false,
                          complete: function () {
                              this.remove()
                          }
                      });
                  }, 1300)
              }
          }, 'linear');
      }
  },
  unitSize: function ({ gm, ml, space = false, str = false }) {

    /*  :------------------------------------:
        | REALTIME DATABASE
        | gm = unidade de medida para gramas
        | ml = unidade de medida para mililitros
        | space = um espaço de uma letra inial
        | str = mostrar texto quando valor vazio
        :------------------------------------:  */

    // Peso indefinido : significa que o valor esperado não é um número, acontecerá devido algum erro ao subir o valor para o banco de dados

    return (typeof gm === 'number' || typeof ml === 'number') ?
      ml === undefined ? gm >= 1000 ? (function () {

        var countGrams = 0;
        var atual = gm;
        function decrementAgain() {
          countGrams = ++countGrams;
          atual = atual - 1000;
          // eslint-disable-next-line no-unused-expressions
          atual >= 1000 ? decrementAgain() : null;
        }

        // eslint-disable-next-line no-unused-expressions
        atual >= 1000 ? decrementAgain() : null;

        if (atual !== 0) {
          return space ? ` ${Number(countGrams + '.' + atual).toFixed(1).replace('.', ',')}kg` : `${Number(countGrams + '.' + atual).toFixed(1).replace('.', ',')}kg`;
        }
        else {
          return space ? ` ${countGrams}kg` : `${countGrams}kg`;
        }
      })() : gm !== 0 ? space ? ` ${gm}g` : ` ${gm}g` : str ? 'Peso não informado' : '' : ml >= 1000 ? (function () {

        var countGrams = 0;
        var atual = ml

        function decrementAgain() {
          countGrams = ++countGrams;
          atual = atual - 1000
          // eslint-disable-next-line no-unused-expressions
          atual >= 1000 ? decrementAgain() : null;
        }

        // eslint-disable-next-line no-unused-expressions
        atual >= 1000 ? decrementAgain() : null

        if (atual !== 0) {
          return space ? ` ${Number(countGrams + '.' + atual).toFixed(1).replace('.', ',')}L` : `${Number(countGrams + '.' + atual).toFixed(1).replace('.', ',')}L`;
        }
        else {
          return space ? ` ${countGrams}L` : `${countGrams}L`;
        }
      })() : space ? ` ${ml}ml` : `${ml}ml` : str ? 'Peso indefinido' : '';
  },
  btnAnimate: function ({ first, second, action, bolean = true }) {

    const INCREMENTdiv = bolean ? first : second;
    const GETTINGdiv = bolean ? second : first;

    $(INCREMENTdiv).animate({ opacity: 0 }, {
      duration: 400,
      easing: 'linear',
      queue: false
    });

    $(INCREMENTdiv).animate({
      'transform': '26'
    }, {
      step: function (now, fx) {
        $(this).css({ "transform": "translate3d(0px, " + now + "px, 0px)" });
      },
      duration: 400,
      easing: 'linear',
      queue: false,
      complete: function () {
        // action()
        // this.removeAttribute('style')
      }
    }, 'linear');

    setTimeout(function () {
      $(GETTINGdiv).fadeIn(400, function () {
        action()
        GETTINGdiv.removeAttribute('style')
        INCREMENTdiv.removeAttribute('style')
      });
    }, 200);
  }
}

export const timeStamp = function (time) {

  const ARRAYdate = new Intl.DateTimeFormat('pt-BR', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric', hour: '2-digit', minute: 'numeric', hour12: true });
  // eslint-disable-next-line
  const [{ value: weekday }, , { value: day }, , { value: month }, , { value: year }, , { value: hour }, , { value: minute }, , { value: hour12 }] = ARRAYdate.formatToParts(new Date(time));

  return { weekday, day, month, year, hour, minute, hour12 }
}
