import React from 'react';
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";

import ComponentSkeletonCardThumb from './ComponentSkeletonCardThumb';

import { Div } from '../styles/Skeleton';
import { Li, Sub } from '../styles/Card';

function ComponentSkeletonMapCard({ data, i }) {

  const { title, unavailable, description } = data;

  return (
    <Li>
      <Sub>
        <Div displayFlex={true} alignItems="center" floatChild="left" style={{ height: '25px' }}>
          <Skeleton width={title} height={12} />
        </Div>
        {description ? (
          <Div displayFlex={true} flexDirection="column" spaceBottomChild="1rem" floatChild="left" style={{ height: '54px' }}>
            <Skeleton width={175} height={9} />
            <Skeleton width={168} height={9} />
            <Skeleton width={61} height={9} />
          </Div>
        ) : null}
        <Div displayFlex={true} alignItems="center" spaceRightChild="1rem" floatChild="left">
          <Div displayFlex={true} alignItems="center" floatChild="left" style={{ height: '18px' }}>
            <Skeleton width={50} height={12} />
          </Div>
        {unavailable ? (
          <SkeletonTheme highlightColor="#dc2020">
            <Div displayFlex={true} floatChild="left" widthChild="100%" style={{ width: '84px' }}>
              <Skeleton width={84} height={24} />
            </Div>
          </SkeletonTheme>
        ) : null}
        </Div>
      </Sub>
      <ComponentSkeletonCardThumb />
    </Li>
  )
}

export default ComponentSkeletonMapCard;
