import React from 'react';
import { useAsync } from "react-async";

import { SnapshotWhere, SnapshotCart } from '../Context';
import { functions } from '../utils/Connection';

import ComponentCartContent from './ComponentCartContent';
import ComponentSkeletonCart from './ComponentSkeletonCart';

const filterValues = ({ val, data }) => {
  const unique = (value, index, self) => {
    return self.indexOf(value) === index;
  };

  return data.map((assoc) => assoc[val]).filter(unique);
};

const fetchPerson = async ({ useWhere, useCart }, { signal }) => await functions()
  .httpsCallable('getProducts')({ where: useWhere, data: filterValues({ val: 'id', data: useCart }) })
  .then((data) => data.data);

function ComponentAwaitCart() {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  // eslint-disable-next-line no-unused-vars
  const { useCart, setCart } = SnapshotCart();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, useCart });

  if (error) return error.message
  if (data) return <ComponentCartContent {...{ data }} />
  return <ComponentSkeletonCart />
}

export default ComponentAwaitCart;
