import React from 'react';
import ReactDOM from 'react-dom';

import { functions } from './utils/Connection';

import App from './App';

// import './index.css';

async function startDatabase() {
  return await functions().httpsCallable('App')({
    where: document.querySelector('[name=local]').content
   })
   .then((data) => data.data);
}

const StartConnection = () => Promise.resolve(startDatabase())

StartConnection()
  .then(function (data) {

    const { hostname, protocol } = new URL(window.location.href);

    // hostname === 'delivery.bracael.com' && protocol === 'https:' // validar para produção !IMPORTANT
    // console.log(hostname === 'localhost' && protocol === 'http:')

    return hostname === 'delivery.bracael.com' && protocol === 'https:' && data !== undefined ? Promise.resolve(data) : Promise.reject()
  })
  .then(function (snapshot) {
    ReactDOM.render(
      <React.StrictMode>
        <App {...snapshot} />
      </React.StrictMode>, document.getElementById('root'));
  })
  .catch(function () {
    ReactDOM.render(<div>ERRO 500</div>, document.getElementById('root'));
  })
