import React from 'react';

import ComponentMapPick from './ComponentMapPick';

import { Content, Title, Head, Name, Sub, Important, Main } from '../styles/Pick';

function ComponentProductPick({ data, i, usePost, setPost }) {

  const id = data[0];
  const { name, limit, important, list = [] } = data[1];

  const { selector = {} } = usePost;
  const bag = selector[id] || [];

  return (
    <Content>
      <Head>
        <Title>
          <Name>{name}</Name>
          <Sub>{(important) ? `${bag.length} de ${limit}` : `Escolha até ${limit} ${limit === 1 ? 'opção' : 'opções'}`}</Sub>
        </Title>
        {(important) ? bag.length === limit ? <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 515.556 515.556" fill="#23b04f"><path d="m0 274.226 176.549 176.886 339.007-338.672-48.67-47.997-290.337 290-128.553-128.552z" /></svg> : <Important>Obrigatório</Important> : null}
      </Head>
      <Main>
        {list.map((item, i) => <ComponentMapPick key={i} {...{ item, i, data, id, usePost, setPost }} />)}
      </Main>
    </Content>
  )
}

export default ComponentProductPick;
