import React from 'react';
import { useHistory } from 'react-router';

import Bracael from '../Bracael';

import ComponentAwaitCartThumb from './ComponentAwaitCartThumb';
import ComponentSkeletonCartThumb from './ComponentSkeletonCartThumb';

import { Info, Price } from '../styles/Cart';

function ComponentMapCart({ assoc, i, data }) {

  const history = useHistory();

  const { id, amount = 1, comment = '', additional = {}, moment } = assoc;
  const { title, weight, grams, price, thumb = [], extra = {} } = data.find((data) => data.id === id);

  const ReduceExtra = Object.keys(additional).length ? Object.entries(additional).map(val => {
    const uid = val[0];
    const { amount } = val[1];
    const { value } = extra[uid];

    return Number(value * amount);
  }).reduce((a, b) => a + b, 0) : 0;

  const ComponentAddStart = () => history.push(`/p/${id}&timestamp=${moment}`);

  return (
    <div onDoubleClick={() => ComponentAddStart()}>
      {thumb.length
        ? <ComponentAwaitCartThumb {...{ thumb }} />
        : <ComponentSkeletonCartThumb />}
      <Info>
        <Price>
          {Number((price * amount) + ReduceExtra).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
          {amount > 1 ? <small>{amount}</small> : null}
          {ReduceExtra || comment ? <div>{ReduceExtra ? <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="#e47a2e"><path fillRule="evenodd" data-name="Plus" d="M14,10H10v4a2,2,0,0,1-4,0V10H2A2,2,0,1,1,2,6H6V2a2,2,0,1,1,4,0V6h4A2,2,0,0,1,14,10Z" /></svg> : null}{comment ? <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="#e47a2e"><path fillRule="evenodd" data-name="Comment" d="M14,12H7L3,16V12H2a2,2,0,0,1-2-2V2A2,2,0,0,1,2,0H14a2,2,0,0,1,2,2v8A2,2,0,0,1,14,12ZM3,3H13V5H3V3ZM3,7H13V9H3V7Z" /></svg> : null}</div> : null}
        </Price>
        <span>{title}</span>
        <span>{weight !== undefined ? Bracael.unitSize({ ml: weight, str: true }) : Bracael.unitSize({ gm: grams, str: true })}</span>
      </Info>
    </div>
  )
}

export default ComponentMapCart;
