import React from 'react';
import { useAsync } from "react-async";

import { SnapshotWhere } from '../Context';
import { functions } from '../utils/Connection';

import ComponentBairros from './ComponentBairros';
import ComponentSkeletonCards from './ComponentSkeletonCards';

const fetchPerson = async ({ useWhere }, { signal }) => {

  return await functions()
    .httpsCallable('getPosts')({ where: `${useWhere}/order` })
    .then((snapshot) => snapshot.data);
}

function ComponentAwaitBairros() {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere });

  if (error) return error.message
  if (data) return <ComponentBairros {...{ data }} />
  return <ComponentSkeletonCards />
}

export default ComponentAwaitBairros;
