import React from 'react';
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";

import { SnapshotConnection } from '../Context';

import { Div } from '../styles/Skeleton';
import { Content } from '../styles/Headsub';

function ComponentSkeletonHeadsub() {

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { status = false } = useConn;

  return (
    <Content>
      <Div displayFlex={true} alignItems="center" floatChild="left" style={{ height: '35px' }}>
        <Skeleton width={114} height={18} />
      </Div>
      <SkeletonTheme highlightColor={status ? '#39ce6d' : '#f13636'}>
        <Div displayFlex={true} floatChild="left">
          <Skeleton width={status ? 136 : 147} height={35} />
        </Div>
      </SkeletonTheme>
    </Content>
  );
}

export default ComponentSkeletonHeadsub;
