import React from 'react';

import Bracael from '../Bracael';

import ComponentMapHome from './ComponentMapHome';
import ComponentHeadsub from '../components/ComponentHeadsub';

import ComponentMenu from '../components/ComponentMenu';

import { Container } from '../styles/Home';

function ComponentHome({ data, configGlobal, screenRef, swipeRef, useMenuId, setMenuId }) {

  return (
    <>
      <ComponentHeadsub {...{ configGlobal }} />
      <ComponentMenu {...{ data, screenRef, swipeRef, useMenuId, setMenuId }} />
      <Container>
        {Object.keys(Bracael.category_names)
        .filter((id) => {

          return !!Object.entries(data)
          .filter((data) => data[1].status === 200 || data[1].status === 403)
          .filter((data) => data[1].category === id).length;
        })
        .map((id) => <ComponentMapHome key={id} {...{ id, data }} />)}
      </Container>
    </>
  )
}

export default ComponentHome;
