import React, { useEffect, useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';

import Bracael from '../Bracael';
import deouncedUse from '../useDebounce';
import smoothScrollTo from '../smooth';

import ComponentHeader from '../components/ComponentHeader';
import ComponentAwaitHome from '../components/ComponentAwaitHome';
import ComponentNav from '../components/ComponentNav';

import { Scroll } from '../styles/root';
import { BottomInfo, Backlinks } from '../styles/Home';

function Home() {

  const history = useHistory();
  const screenRef = useRef(null);
  const swipeRef = useRef(null);
  const [useHead, setHead] = useState(screenRef.current ? screenRef.current.scrollTop : 0 > 100);
  const [useMenuId, setMenuId] = useState('001');

  const configGlobal = {
    name_content: "Cardápio",
    title_content: "Cardápio",
    id_feed: "001"
  };

  const onChangeFn = (current) => {

    let i = 0;
    const data = current.querySelectorAll('[data-section]');
    const loopSection = (data) => Object({
      scrollHeight: data.scrollHeight,
      offsetTop: data.offsetTop,
      offsetBottom: data.offsetTop + data.offsetHeight,
      offsetHeight: data.offsetHeight,
      id: data.dataset.section
    });

    const scrollTop = current.scrollTop + 120;

    while (i < data.length) {

      // eslint-disable-next-line no-unused-vars
      const { scrollHeight, offsetTop, offsetBottom, offsetHeight, id } = loopSection(data[i]);

      if (offsetBottom > scrollTop && offsetTop < scrollTop) {
          setMenuId(id);

        break;
      }
      else {
        if (offsetTop > scrollTop && scrollTop < offsetBottom) {
            setMenuId(id);

          break;
        };
      };

      ++i;
    };
  };

  const debouncedChange = deouncedUse(onChangeFn, 200);
  const Shrink = current => current.scrollTop > 80 ? (useHead !== true) && setHead(true) : (useHead !== false) && setHead(false)

  const onScrollFn = (event) => {

    Shrink(event.target);
    // onChangeFn(event.target);

    if (event.target.querySelectorAll('[data-section]').length) {
      debouncedChange(event.target);
    };
  };

  useEffect(() => {
    if (swipeRef.current) {
      if (useHead) {
        swipeRef.current.style.boxShadow = "rgba(153,150,150, 0.15) 0px 4px 4px";
      }
      else {
        if (swipeRef.current) {
          swipeRef.current.style.boxShadow = null;
        };
      };
    };
  }, [useHead]);

  useEffect(() => {
    if (swipeRef.current) {
      swipeRef.current.childNodes.forEach((data) => {
        if (useMenuId === Object.keys(Bracael.category_names)[Object.values(Bracael.category_names).indexOf(data.innerText)]) {
          smoothScrollTo(data.offsetLeft - 20, 0, 400, swipeRef.current);
        }
      });
    };
  }, [useMenuId]);

  return (
    <>
      <Scroll ref={screenRef} onScroll={onScrollFn}>
        <ComponentHeader />
        <ComponentAwaitHome {...{ configGlobal, screenRef, swipeRef, useMenuId, setMenuId }} />
        <BottomInfo>
          <div>Qualquer divergência de valores dos produtos ou taxa de entrega poderá ser ajustado direto com o estabelecimento.</div>
          <div>Todos os preços estão em BRL</div>
        </BottomInfo>
        <Backlinks>
          <div><button onClick={() => history.push('/termos')}>Termos &amp; Condições</button></div>
          <div><button onClick={() => history.push('/privacidade')}>Política de Privacidade</button></div>
          <div><button onClick={() => history.push('/cookies')}>Política de cookies</button></div>
          <div><button onClick={() => alert('Bracael.COM\nVersão Beta, 12.05.2020\nO Conteúdo para está página estará disponivel em breve.')}>Enviar feedback</button></div>
        </Backlinks>
      </Scroll>
      <ComponentNav />
    </>
  )
}

export default Home;
