import React, { useState } from 'react';

import ComponentMapBairros from './ComponentMapBairros';

import { Content, Small } from '../styles/Bairros';

function ComponentBairros({ data }) {

  // eslint-disable-next-line no-unused-vars
  const [useCache, setCache] = useState(data);

  return (
    <Content>
      {Object.keys(useCache).length
      ? Object.entries(useCache).map((data) => <ComponentMapBairros key={data[0]} {...{ data }} />)
      : <Small>Não há nenhum bairro publicado no momento.</Small>}
    </Content>
  );
}

export default ComponentBairros;
