import React from 'react';

import ComponentMapCard from './ComponentMapCard';

import { Content } from '../styles/Card';

function ComponentCards({ data, configGlobal }) {

  const { id_feed } = configGlobal;
  const organize = (a, b) => (a[1].published < b[1].published) ? 1 : ((b[1].published < a[1].published) ? -1 : 0);

  return (
    <Content>
      {Object.entries(data)
      .filter((data) => data[1].category === id_feed && (data[1].status === 200 || data[1].status === 403))
      .sort(organize)
      .map((data) => <ComponentMapCard key={data[0]} {...{ data }} />)}
    </Content>
  );
}

export default ComponentCards;
