import React from 'react';

import { SnapshotCart } from '../Context';

import { Icon } from '../styles/Nav';

function ComponentIconCart() {

  // eslint-disable-next-line no-unused-vars
  const { useCart, setCart } = SnapshotCart();

  return Object.keys(useCart).length ? <Icon>{Object.entries(useCart).map(data => {
    const { amount = 1 } = data[1];

    return amount
  }).reduce((a, b) => a + b, 0)}</Icon> : null;
}

export default ComponentIconCart;
