import React from 'react';
import { useHistory } from 'react-router';

import ComponentAwaitCardThumb from './ComponentAwaitCardThumb';
import ComponentSkeletonCardThumb from './ComponentSkeletonCardThumb';

import { Li, Sub, Description, Value, Price, Button, Unavailable } from '../styles/Card';

function ComponentMapCard({ data }) {

  const history = useHistory();

  const id = data[0]
  // eslint-disable-next-line no-unused-vars
  const { category, status, description, extra, favorite, grams, price, published, purchase, thumb = [], title, weight } = data[1];

  return (
    <Li>
      <Sub>
        <h4>{title}</h4>
        {description ? <Description>{description}</Description> : null}
        <Value>
          <Price>{Number(price).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Price>
          {status === 403 ? <Unavailable>Indisponível</Unavailable> : null}
        </Value>
      </Sub>
      {thumb.length
      ? <ComponentAwaitCardThumb {...{ thumb }} />
      : <ComponentSkeletonCardThumb />}
      {status !== 403 ? <Button type="button" onClick={() => history.push(`/p/${id}`)}></Button> : null}
    </Li>
  )
}

export default ComponentMapCard;
