import React from 'react';

import Bracael from '../Bracael';

import ComponentSkeletonHeadsub from './ComponentSkeletonHeadsub';
import ComponentSkeletonMenu from './ComponentSkeletonMenu';
import ComponentSkeletonMapHome from './ComponentSkeletonMapHome';

import { Container } from '../styles/Home';

function ComponentSkeletonHome({ swipeRef }) {

  return (
    <>
      <ComponentSkeletonHeadsub />
      <ComponentSkeletonMenu {...{ swipeRef }} />
      <Container>
        {Object.keys(Bracael.category_names)
        .map((id) => <ComponentSkeletonMapHome key={id} />)}
      </Container>
    </>
  )
}

export default ComponentSkeletonHome;
