import React from 'react';

import { SnapshotCart } from '../Context';

import ComponentAwaitCart from './ComponentAwaitCart';

import { Cart, Empty } from '../styles/Cart';

function ComponentCart() {

  // eslint-disable-next-line no-unused-vars
  const { useCart, setCart } = SnapshotCart();

  return (
    <Cart>
      {Object.keys(useCart).length
      ? (useCart.length & 1)
        ? <ComponentAwaitCart key={1} />
        : <ComponentAwaitCart key={2} />
      : <Empty>Por enquanto, não há nada por aqui.</Empty>}
    </Cart>
  )
}

export default ComponentCart;
