import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Home from '../routes/Home';
import Hamburguer from '../routes/Hamburguer';
import Promocoes from '../routes/Promocoes';
import Combos from '../routes/Combos';
import Porcoes from '../routes/Porcoes';
import Bebidas from '../routes/Bebidas';
import Bomboniere from '../routes/Bomboniere';
import Confirma from '../routes/Confirma';
import Sobre from '../routes/Sobre';
import Termos from '../routes/Termos';
import Politica from '../routes/Politica';
import Destaque from '../routes/Destaque';
import Bairros from '../routes/Bairros';
import ProductId from '../routes/ProductId';

function ComponentRouters() {

  return (
    <Switch>
      <Route path="/" exact render={() => <Home />} />
      <Route path="/hamburguer" render={() => <Hamburguer />} />
      <Route path="/promocoes" render={() => <Promocoes />} />
      <Route path="/combos" render={() => <Combos />} />
      <Route path="/porcoes" render={() => <Porcoes />} />
      <Route path="/bebidas" render={() => <Bebidas />} />
      <Route path="/bomboniere" render={() => <Bomboniere />} />
      <Route path="/confirma" render={() => <Confirma />} />
      <Route path="/sobre" render={() => <Sobre />} />
      <Route path="/termos" render={() => <Termos />} />
      <Route path="/politica" render={() => <Politica />} />
      <Route path="/destaque" render={() => <Destaque />} />
      <Route path="/bairros" render={() => <Bairros />} />
      <Route path="/p/:id" render={() => <ProductId />} />
    </Switch>
  );
}

export default ComponentRouters;
