import React from 'react';
import { useAsync } from 'react-async';

import { SnapshotWhere } from '../Context';
import { storage } from '../utils/Connection';

import ComponentProductThumb from '../components/ComponentProductThumb';
import ComponentSkeletonLoadThumb from '../components/ComponentSkeletonLoadThumb';

const defaultGravatar = 'data:image/svg+xml;base64,';

async function addImageProcess(val){
  const img = new Image();
  img.src = val;
  await img.decode();
  return {
    width: String(img.width),
    height: String(img.height),
    src: img.src
  };
};

const awaitStorage = async (data, where) => {

  async function onResolve(foundURL) {
    return await addImageProcess(foundURL);
  };

  // ANCHOR verificar o status de erro no front-end
  function onReject(error) {
    return {
      error: true
    };
  };

  return await Promise.all(data.map(async (data, i) => {

    const { image_name, image_format } = data;

    return {
      thumbnail: await storage()
        .ref(`/${where}/products/thumbs`)
        .child(image_name + '_640x640.jpeg')
        .getDownloadURL()
        .then(onResolve, onReject)
        .catch((error) => defaultGravatar),
      original: await storage()
        .ref(`/${where}/products`)
        .child(image_name + '.' + image_format)
        .getDownloadURL()
        .then(onResolve, onReject)
        .catch((error) => defaultGravatar)
    }
  }));
};

const fetchPerson = async ({ useWhere, thumb }, { signal }) => {
  return await awaitStorage(thumb, useWhere);
};

function ComponentAwaitProductThumb({ thumb }) {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, thumb });

  if (error) return error.message
  if (data) return <ComponentProductThumb {...{ data }} />
  return <ComponentSkeletonLoadThumb />
}

export default ComponentAwaitProductThumb;
