import React from 'react';

import ComponentMapAdditional from './ComponentMapAdditional';

import { Content, Title, Main } from '../styles/Additional';

function ComponentProductAdditional({ extra, usePost, setPost }) {

  return (
    <Content>
      <Title>Adicionais?</Title>
      <Main>
        {Object.entries(extra).map((data, i) => <ComponentMapAdditional key={data[0]} {...{ data, i, usePost, setPost }} />)}
      </Main>
    </Content>
  )
}

export default ComponentProductAdditional;
