import React from 'react';

import Bracael from '../Bracael';

import ComponentSkeletonMenuItens from './ComponentSkeletonMenuItens';

import { Content } from '../styles/Menu';

function ComponentSkeletonMenu({ swipeRef }) {

  return (
    <Content draggable="true" ref={swipeRef}>
      {Object.entries(Bracael.category_names)
      .map((data) => <ComponentSkeletonMenuItens key={data[0]} {...{ data }} />)}
    </Content>
  )
}

export default ComponentSkeletonMenu;
