import React from 'react';

import { SnapshotConnection } from '../Context';

import { Content, Title, Status } from '../styles/Headsub';

function ComponentHeadsub({ configGlobal }) {

  const { name_content } = configGlobal;

  // eslint-disable-next-line no-unused-vars
  const { useConn, setConn } = SnapshotConnection();
  const { status = false } = useConn;

  return (
    <Content>
      <Title>{name_content}</Title>
      <Status status={status}>{status ? 'Delivery aberto!' : 'Delivery fechado!'}</Status>
    </Content>
  );
}

export default ComponentHeadsub;
