import React, { useEffect,  } from 'react';

import { Modal, Content, Title, Main, Description, Footer, Filled, Outline, Text } from '../styles/OnCart';

const { jQuery: $ } = window;

function ComponentOnCart({ useUpdate, setUpdate, useStatus, useSucessLayout, useMoment, setMoment, usePost, setPost, setAmount, setComment, setSelector, setAdditional }) {

  const { id, amount = 1, comment = '', additional = {}, selector = {} } = usePost;

  useEffect(() => {
    if (useStatus.current && useUpdate) {
      $(useSucessLayout.current).fadeIn(200);
    };
  }, [useStatus, useSucessLayout, useUpdate]);

  const altNew = () => {
    setUpdate(false);
    setMoment(new Date().toISOString());
    setPost({ id });
    setAmount(1);
    setComment('');
    setSelector(JSON.stringify({}));
    setAdditional(JSON.stringify({}));
  };

  const altFollow = () => {
    setUpdate(false);
    setAmount(amount);
    setComment(comment);
    setSelector(JSON.stringify(selector));
    setAdditional(JSON.stringify(additional));
  };

  return (
    <Modal ref={useSucessLayout}>
      <Content>
        <Main>
          <Title>Já esta na sacola!</Title>
          <Description>Certo, agora você quer continuar nessa compra ou deseja uma nova compra?</Description>
          <Footer>
            <Filled type="button" onClick={() => altFollow()}>
              <Text>Continuar</Text>
            </Filled>
            <Outline type="button" onClick={() => altNew()}>
              <Text>Quero um novo</Text>
            </Outline>
          </Footer>
        </Main>
      </Content>
    </Modal>
  );
}

export default ComponentOnCart;
