import React from 'react';
import { useAsync } from "react-async";
import { useParams } from 'react-router-dom';

import { SnapshotWhere } from '../Context';
import { functions } from '../utils/Connection';

import ComponentProductId from '../components/ComponentProductId';
import ComponentSkeletonProductId from '../components/ComponentSkeletonProductId';

const repairUrl = (id) => {
  return id.indexOf('&') > -1
    ? id.substring(id.indexOf('&'), 0)
    : id;
};

const fetchPerson = async ({ useWhere, id }, { signal }) => await functions()
  .httpsCallable('getPosts')({ where: `${useWhere}/feed/${repairUrl(id)}` })
  .then((data) => data.data);

function ProductId() {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { id } = useParams();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, id });

  if (error) return error.message
  if (data) return <ComponentProductId {...{ data, id }} />
  return <ComponentSkeletonProductId />
};

export default ProductId;
