import React from 'react';

import Bracael from '../Bracael';
import smoothScrollTo from '../smooth';

import { Button } from '../styles/Menu';

function ComponentMenuItens({ id, screenRef, useMenuId, setMenuId }) {

  const getScrollToByButton = () => {
    return screenRef.current.querySelector('[data-section="'+id+'"]').offsetTop;
  };

  const scrollToIdOnClick = (event) => {
    const to = getScrollToByButton() - 80;
    smoothScrollTo(0, to, 400, screenRef.current);
  };

  return (
    <Button type="button" status={useMenuId === id} onClick={(e) => scrollToIdOnClick(e)}>
      {Bracael.category_names[id]}
    </Button>
  )
}

export default ComponentMenuItens;
